body {
  margin: 0;
  padding: 0;
  height: 100vh;
  background-image: url(/weather-app-react/static/media/abstract-art-background-1040473.6e8ba9f3.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fdf5fc;
  font-family: 'Ubuntu', sans-serif;
}

.searchBar-container {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
}

.searchBar-form {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
    padding: 5px 25px;
    text-align: center;
}

.searchBar-container input, .searchBar-container button {
    border-radius: 5px;
    border-style: solid;
    font-size: 18px;
    color: #222;
    box-shadow: 10px 15px 15px 5px rgba(0,0,0,0.75);
}

.searchBar-container input {
    width: 30%;
    height: 35px;
    padding-left: 7px;
    background-color: #fdf5fc;
    border-color: #fdf5fc;
}

.searchBar-container button {
    width: 10%;
    height: 36px;
    margin-left: 10px;
    background-color: #84b000;
    color: #fdf5fc;
    transition: all ease-in-out 0.4s;
}

.searchBar-container button:hover {
    background-color: #fdf5fc;
    color: #84b000;
    cursor: pointer;
}

@media (min-width: 992px) and (max-width: 1199px) {
    .searchBar-container input {
        width: 35%;
    }
    .searchBar-container button {
        width: 12%;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .searchBar-container input {
        width: 45%;
    }
    .searchBar-container button {
        width: 15%;
    }
} 

@media (min-width: 600px) and (max-width: 767px) {
    .searchBar-container {
        width: 90%;
        padding: 15px;
    }
    .searchBar-container input {
        width: 55%;
    }
    .searchBar-container button {
        width: 18%;
    }
} 

@media (min-width: 480px) and (max-width: 599px) {
    .searchBar-container {
        width: 95%;
        padding: 10px;
    }
    .searchBar-container input {
        width: 55%;
    }
    .searchBar-container button {
        width: 20%;
    } 
}

@media (max-width: 480px) {
    .searchBar-container {
        width: 95%;
        padding: 10px;
    }
    .searchBar-form {
        width: 95%;
    }
    .searchBar-container input {
        width: 63%;
    }
    .searchBar-container button {
        width: 32%;
    } 
}

.header-container {
    border: 1px solid #fdf5fc;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-size: 160%;
    background-color: rgba(253,245,252,0.2);
    box-shadow: 10px 15px 15px 5px rgba(0,0,0,0.75);
}

@media (min-width: 768px) and (max-width: 991px) {
    .header-container {
        width: 85%;
    }
} 
@media (max-width: 767px) {
    .header-container {
        width: 85%;
        font-size: 170%;
    }
} 
@media (max-width: 480px) {
    .header-container {
        width: 90%;
        font-size: 140%;
        margin-bottom: 20px;
    }
}

* {
    box-sizing: border-box;
}

.display-container {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 30px;
    margin-bottom: 50px;
}

h3 {
    font-size: 26px;
    text-align: center;
}

.display-box {
    border: 3px solid #fdf5fc;
    width: 65%;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
    background-color: rgba(0,0,0,0.3);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    box-shadow: 10px 15px 15px 5px rgba(0,0,0,0.75);
}

.display-header {
    border-bottom: 1px solid #fdf5fc;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    font-size: 26px;
    margin-bottom: 10px;
    text-transform: uppercase;
}

.temp-box {
    -webkit-flex-basis: 35%;
            flex-basis: 35%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 15px;
}

.other-detail-box {
    -webkit-flex-basis: 65%;
            flex-basis: 65%;
}

.temp-detail {
    height: 75px;
    width: 75px;
    font-size: 48px;
    padding: 10px;
}

.temp-icon {
    border: 2px solid #fdf5fc;
    border-radius: 50%;
    text-align: center;
}

.other-detail-box {
    border-left: 2px solid #fdf5fc;
    padding: 15px;
}

.sun-rise-set {
    font-size: 24px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding-bottom: 5px;
    border-bottom: 1px solid #fdf5fc;
}

.weater-detail-div-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    text-align: start;
    padding: 10px;
    font-size: 20px;
}

.weather-detail-div {
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
    padding-top: 10px;
}

