@media (min-width: 480px) and (max-width: 599px) {
    .display-container {
        width: 85%;
    }
    .weather-detail-box {
        -webkit-flex-basis: 100%;
                flex-basis: 100%;
    }
    .other-detail-box {
        border-top: 1px solid #fdf5fc;
        border-left: 0;
    }
    .temp-box {
        -webkit-justify-content: center;
                justify-content: center;
        padding-left: 0;
        padding: 15px;
    }
    .display-header {
        font-size: 22px;
        margin-bottom: 10px;
        padding-bottom: 10px;
    }
